const {ccclass, property} = cc._decorator;

@ccclass
export default class NewClass extends cc.Component {

    @property(cc.Node)
    nodes: cc.Node[] = [];

    // LIFE-CYCLE CALLBACKS:

    // onLoad () {}

    startTweenAnimation () {
        let nodes = this.nodes;
        for (let i = 0; i < nodes.length; i++) {
            let node = nodes[i];
            cc.tween(node)
                .delay(0.5 + i * 0.2)
                .repeat(3,
                    cc.tween(node)
                        .set({ opacity: 0, scale: 10, x: 0, angle: 0 })
                        .parallel(
                            cc.tween().to(1, { opacity: 255, scale: 1 }, { easing: 'quintInOut' }),
                            cc.tween().to(2.5, { x: node.x }, { easing: 'backOut' })
                        )
                        .delay(0.5)
                        .to(0.8, { angle: 360 }, { easing: 'cubicInOut' })
                        .delay(1)
                        .to(0.3, { opacity: 0, scale: 3 }, { easing: "quintIn" })
                        .delay(1)
            )
            .call(()=>{
                console.log(`${node.name} tween animation end`);
            })
            .start()
        }
    }

    // update (dt) {}
}
